import { Injectable } from '@angular/core';
import { Chapter } from './model/chapter';
import { AngularFireDatabase } from 'angularfire2/database';
import { About } from './model/about';
import { Comment } from './model/comment';
import { DatePipe } from '@angular/common';


@Injectable({
  providedIn: 'root'
})
export class DatabaseService {


  constructor(private afDatabase: AngularFireDatabase, private datePipe: DatePipe) { }

  public getAbout() {
    return this.afDatabase.object<About>('/about').valueChanges();
  }

  public getLastPublication() {
    return this.afDatabase.object<String>('/last_publication').valueChanges();
  }
  public getChapters() {
    return this.afDatabase.list<Chapter>('/chapters');
  }

  public getComments() {
    return this.afDatabase.list<Comment>('/comments');
  }

  public addChapter(newChapter: Chapter) {
    this.afDatabase.object<Chapter>('/chapters/'+(newChapter.number-1)).update(newChapter);
  }

  public addImage(chapter:Chapter){
    let currentDate = this.datePipe.transform(new Date(), 'dd/MM/yyyy');
    this.afDatabase.object<String>('/last_publication').set(currentDate);
    this.afDatabase.object<Chapter>('/chapters/'+(chapter.number-1)).update(chapter);
  }

  public addComment(author: String, comment: String) {
    let newComment = new Comment();
    newComment.author = author;
    newComment.comment = comment;
    let currentDate = this.datePipe.transform(new Date(), 'dd/MM/yyyy');
    newComment.date = currentDate;
    this.afDatabase.list<Comment>('/comments').push(newComment);

  }

}
