import { Component, OnInit } from '@angular/core';
import { SlideshowService } from '../slideshow.service';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { DatabaseService } from '../database.service';
import {NgForm, FormControl, Validators} from '@angular/forms';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {

  lastPublication: Observable<String>;
 
  constructor(private slideshowService:SlideshowService,private databaseService:DatabaseService,private router:Router) {
    this.lastPublication=databaseService.getLastPublication();
  
  }

  ngOnInit() {
  }

  public gotoChapter(chapter:number){
    this.slideshowService.gotoChapter(chapter);
  }

  public onSubmit(form: NgForm) {
    this.slideshowService.gotoPage(+form.value['numPage']);
}

}
