import { Component, OnInit } from '@angular/core';
import { DatabaseService } from '../database.service';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { SlideshowService } from '../slideshow.service';
import { AngularFireAuth } from 'angularfire2/auth';
import { auth } from 'firebase/app';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {

  newFile: File;
  constructor(private databaseService: DatabaseService, private afAuth: AngularFireAuth, private slideshowService: SlideshowService, private router: Router) {
  }

  ngOnInit() {
  }


  login() {
    this.afAuth.auth.signInWithPopup(new auth.GoogleAuthProvider()).catch(function (error) { console.log("--------sign in with popup error ----", error.message); });
  }
  logout() {
    this.afAuth.auth.signOut();
  }

  public onSubmit(form: NgForm) {


    if (form.value['chapitre'] !== "") {
      this.slideshowService.addChapter(form.value['chapitre']);
    }

    if (this.newFile) {
      this.slideshowService.addImage(this.newFile);
    }


    this.router.navigate(['slideshow']);
  }

  detectFiles(event) {
    this.newFile = event.target.files[0];
  }



}
