import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutProjectComponent } from './about-project/about-project.component';
import { SlideShowComponent } from './slide-show/slide-show.component';
import { AboutAuthorComponent } from './about-author/about-author.component';
import { SeeCommentsComponent } from './see-comments/see-comments.component';
import { PublishCommentComponent } from './publish-comment/publish-comment.component';
import { AdminComponent } from './admin/admin.component';

const routes: Routes = [
  { path: '', redirectTo :'/slideshow' ,pathMatch: 'full'},
  { path: 'slideshow', component: SlideShowComponent },
  { path: 'about/aboutProject', component: AboutProjectComponent },
  { path: 'about/aboutAuthor', component: AboutAuthorComponent },
  { path: 'seeComments', component: SeeCommentsComponent },
  { path: 'suite', component: AdminComponent },
  { path: 'publishComment', component: PublishCommentComponent }


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
